.register {
  width: auto;
  max-width: 380px;
  min-width: 300px;
  background: $background-color;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 0.5rem;
  padding: 4.75rem 3.625rem 3.75rem;
}
.register__title {
  padding-bottom: 2.5rem;
}
.register__name {
  padding-bottom: 1.25rem;
}

.register__surname {
  padding-bottom: 1rem;
}

.register__gender {
  padding-bottom: 1.75rem;
}

.register__birth {
  padding-bottom: 2rem;
}

.register__email {
  padding-bottom: 1.375rem;
}

.register__password {
  padding-bottom: 0.875rem;
}

.register__toggle {
  padding-bottom: 1.25rem;
}

.register__arrow-button {
  padding-bottom: 3.625rem;
}

.register__sign-in {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 380px) {
  .register {
    padding: 15px;
  }
}
