.input-text {
  width: auto;
  max-width: 320px;
  min-width: 260px;
}

.input-text__info {
  display: flex;
  justify-content: space-between;
  line-height: 23px;
}

.input-text__name {
  align-self: center;
}

.input-text__state {
  align-self: flex-start;
  font-weight: normal;
  color: $dark50;
  line-height: 18px;
}

// default

.input-text__inp {
  display: block;
  width: 100%;
  height: 44px;
  border: 1px solid $dark25;
  border-radius: 4px;
  background: $background-color;
  padding-left: 15px;
}
.input-text__inp::placeholder {
  color: $dark25;
}

// hover/focus

.input-text__inp:hover {
  border-color: $dark50;
}

.input-text__inp:focus,
.input-text__inp_focus {
  border-color: $dark50;
  outline: none;
}

.input-text__inp:hover::placeholder {
  color: $dark75;
  font-size: 13.5px;
}

.input-text__inp:focus::placeholder,
.input-text__inp_focus::placeholder {
  color: $dark75;
  font-size: 13.5px;
}
