.rich-checkbox {
  width: 264px;
  line-height: 18px;
  color: $dark50;
}

.rich-checkbox__title {
  padding-bottom: 16px;
}

.rich-checkbox__descript {
  padding-left: 30px;
  font-size: 12px;
  line-height: 14px;
  padding-bottom: 10px;
}

/*   input c type='checkbox' */
.rich-checkbox__input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

/*   label,   .rich-checkbox__input */

.rich-checkbox__input + label {
  display: inline-flex;
  align-items: flex-start;
  user-select: none;
  font-weight: bold;
  line-height: 18px;
  color: $dark75;
}

/*   label  before    */

.rich-checkbox__input + label::before {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  flex-grow: 0;
  background: $background-color;
  border: 1px solid $dark25;
  box-sizing: border-box;
  border-radius: 4px;
  margin-right: 10px;
  background-repeat: no-repeat;
  background-position: center center;
}

/*      checkbox */
.rich-checkbox__input:not(:disabled):not(:checked) + label:hover::before {
  border-color: $dark50;
}

/*     (   ) */
.rich-checkbox__input:not(:disabled):active + label::before {
  background-color: $dark5;
  border-color: $dark5;
}

/*   ,         checked */
.rich-checkbox__input:focus:not(:checked) + label::before {
  border-color: $dark50;
}

/*   ,    */
.rich-checkbox__input:focus + label::before {
  border-color: $primary-color;
}

/*   ,    checked */
.rich-checkbox__input:checked + label::before {
  background-image: url("#{$publicPath}/images/checkbox.svg");
}

/*   ,    disabled */
.rich-checkbox__input:disabled + label::before {
  background-color: $dark5;
}
