.booking {
  width: auto;
  max-width: 380px;
  min-width: 300px;
  background: $background-color;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 0.5rem;
  padding: 5rem 3.625rem 3.75rem;
  &__roominfo {
    padding-top: 0.25rem;
    padding-bottom: 2rem;
  }
  &__room-numsign {
    font-family: Quicksand;
    font-weight: bold;
    color: $dark100;
  }
  &__room-number {
    font-family: Quicksand;
    font-weight: bold;
    font-size: 3rem;
    color: $dark100;
  }
  &__room-cat {
    color: $primary-color;
    margin-left: 0.625rem;
  }
  &__room-price {
    padding-top: 0.5rem;
    float: right;
    font-weight: bold;
    color: $dark50;
  }

  &__perday {
    font-size: 1.5375rem;
    font-weight: normal;
    padding-left: 0.375rem;
  }

  &__date-dropdown {
    padding-bottom: 2rem;
  }

  &__guest-dropdown {
    padding-bottom: 2.375rem;
  }

  &__tariff1 {
    padding-bottom: 1.25rem;
    text-align: left;
  }

  &__tariff2 {
    padding-bottom: 1.375rem;
    text-align: left;
  }

  &__tariff3 {
    padding-bottom: 3.125rem;
    text-align: left;
    position: relative;
  }

  &__main,
  &__services,
  &__additional {
    display: inline-block;
    width: 26.75rem;
    line-height: 2.625rem;
  }

  &__sum1,
  &__sum2,
  &__sum3 {
    float: right;
  }

  &__info-button {
    position: absolute;
    user-select: none;
    font-weight: bold;
    font-size: 1.5rem;
    color: $dark25;
    display: inline-block;
    text-align: center;
    line-height: 2.5rem;
    width: 2.5rem;
    height: 2.5rem;
    border: 1px solid $dark25;
    border-radius: 50%;
    margin-left: 0.75rem;
    margin-top: 0.25rem;
  }

  &__total {
    padding-bottom: 2.5rem;
  }

  &__total-line {
    display: inline-block;
    width: 22.75rem;
    height: 1px;
    border-bottom: 1px dashed rgba(31, 32, 65, 0.25);
    margin-right: 0.75rem;
  }
}

@media (max-width: 380px) {
  .booking {
    padding: 15px;
  }
}
